<template>
	<view class="board">
		<view class="box">
			<view class="title">劳务公司列表</view>
			<searchBox :showBroker="true" @handleSearch="handleSearch"></searchBox>
			<view class="table_wrap">
				<view class="tabs flex flex_start">
					<view class="tab" v-for="(item,index) in tabs" :class="currentTab.value==item.value?'active':''"
						@click="changeTab(item)">{{item.text}}</view>
				</view>
				<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
					<uni-tr style="background: #f6f6f6;">
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">ID</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">公司名称</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">信用代码</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">法人姓名</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">联系电话</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">所在城市</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">创建时间</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作</uni-th>
					</uni-tr>
					<uni-tr v-for="(item,index) in list">
						<uni-td align="center">{{item.id}}</uni-td>
						<uni-td align="center">{{item.name}}</uni-td>
						<uni-td align="center">{{item.unified_social_credit_code}}</uni-td>
						<uni-td align="center">{{item.legal_person_name}}</uni-td>
						<uni-td align="center">{{item.legal_person_mobile}}</uni-td>
						<uni-td align="center">{{item.city}}</uni-td>
						<uni-td align="center">{{item.status=="normal"?"正常":"已冻结"}}</uni-td>
						<uni-td align="center">{{item.create_time}}</uni-td>
						<uni-td style="padding:8px 0px;">
							<view class="ope flex flex_around">
								<view class="ope_item" @click="toAdd(item)">添加账号</view>
								<view class="ope_item" @click="handleAccount(item)">{{item.status=="normal"?"冻结":"解冻"}}
								</view>
								<view class="ope_item" @click="navigate({pageName:'broker_detail'},item.id)">详情</view>
							</view>
						</uni-td>
					</uni-tr>
				</uni-table>
				<pageBox :page="pagination" @toNext="toNext"></pageBox>
			</view>
		</view>
		<view class="mask" v-if="showMask">
			<view class="inner">
				<view class="title">添加账号 <text
						style="color:#ffff00;position: relative;top:0;font-size: 16px;">（{{currentCompany.name}}）</text>
				</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="line flex flex-start">
						<view class="tit">姓名</view>
						<view class="input_wrap">
							<input type="text" v-model="name" placeholder="请输入业务员姓名" />
						</view>
					</view>
					<view class="line flex flex-start">
						<view class="tit">身份证号</view>
						<view class="input_wrap">
							<input type="text" v-model="idcard" placeholder="请输入业务员身份证号" />
						</view>
					</view>
					<view class="line flex flex-start">
						<view class="tit">电话</view>
						<view class="input_wrap">
							<input type="text" v-model="mobile" placeholder="请输入业务员电话" />
						</view>
					</view>
					<view class="line flex flex-start">
						<view class="tit">城市</view>
						<view class="input_wrap">
							<input type="text" v-model="city" placeholder="请输入业务员所在城市" />
						</view>
					</view>
					<view class="line flex flex_end">
						<view class="btn" @click="confirmAdd">确认添加</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import searchBox from "@/components/search_box.vue"
	import pageBox from "@/components/paging_box.vue"
	export default {
		name: "broker_list",
		data() {
			return {
				timeStr: "上传",
				list: [],
				tabs: [{
						value: "",
						text: "全部"
					},
					{
						value: "normal",
						text: "正常"
					},
					{
						value: "frozen",
						text: "冻结"
					}
				],
				pagination: {},
				showMask: false,
				currentTab: {
					value: "",
					text: "全部"
				},
				currentPage: 1,
				id: "",
				searCompany: "",
				name: "",
				idcard: "",
				mobile: "",
				city: "",
				currentCompany: {

				}
			};
		},
		components: {
			searchBox,
			pageBox
		},
		created() {
			this.getList()
		},
		methods: {
			handleAccount(item) {
				let _this = this
				let url = ""
				if (item.status == "normal") {
					url = "/admin/company/" + item.id + "/freeze"
					uni.showModal({
						title: "冻结",
						content: "是否确认冻结账户：" + item.name + "？",
						success(resp) {
							if (resp.confirm) {
								_this.$request(url, {}, "POST").then(res => {
									if (res.code == 0) {
										uni.showToast({
											title: "该账户已冻结",
											icon: "none",
											duration: 3000
										})
										_this.getList()
									}
								})
							}
						}
					})
				} else {
					url = "/admin/company/" + item.id + "/unfreeze"
					uni.showModal({
						title: "解冻",
						content: "是否确认解冻账户：" + item.name + "？",
						success(resp) {
							if (resp.confirm) {
								_this.$request(url, {}, "POST").then(res => {
									if (res.code == 0) {
										uni.showToast({
											title: "该账户已解冻",
											icon: "none",
											duration: 3000
										})
										_this.getList()
									}
								})
							}
						}
					})
				}
			},
			confirmAdd() {
				let data = {
					"name": this.name,
					"id_card_number": this.idcard,
					"mobile": this.mobile,
					"city": this.city
				}
				let url = "/admin/company/" + this.currentCompany.id + "/brokers"
				this.$request(url, data, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: "业务员添加成功",
							duration: 3000
						})
						this.resetData()
						this.close()
					}
				})
			},
			resetData() {
				this.name = ""
				this.idcard = ""
				this.city = ""
				this.mobile = ""
				this.currentCompany = {

				}
			},
			toNext(e) {
				this.currentPage = e
				this.getList()
			},
			toAdd(item) {
				this.showMask = true
				this.currentCompany = item
			},
			changeTab(item) {
				this.currentTab.value = item.value
				this.currentTab.text = item.text
				this.setPageName({
					pageName: "broker_list"
				})
				this.getList()
			},
			navigate(obj, id) {
				this.setPageName(obj, id)
				this.$emit("getPageName", obj)
			},
			getList() {
				uni.showLoading({
					title: "努力加载中~"
				})
				let url = "/admin/companies?page=" + this.currentPage + "&status=" + this.currentTab.value + "&name=" +
					this.searCompany
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						this.pagination = res.data.pagination
					}
				})
			},
			handleSearch(e) {
				this.searCompany = e.brokerName
				this.getList()
			},
			close() {
				this.showMask = false
			},
		}
	}
</script>

<style lang="scss">
	::v-deep {
		.search_box {
			margin: 0;
		}
	}

	.active {
		color: #0da81f;
	}

	.disable {
		color: #f00;
	}

	.disable {
		color: #f00;
	}

	.ope {
		position: relative;
		padding: 0 10px;
		width: 100%;
		box-sizing: border-box;

		.more {
			position: absolute;
			top: calc(100% + 8px);
			left: 0;
			width: 100%;
			z-index: 9;
			background: #fff;
			border-radius: 5px;
			box-shadow: 2px 2px 5px 3px #ddd;
			padding: 5px 0;

			.opera {
				text-align: center;
				height: 30px;
				line-height: 30px;
				cursor: pointer;
			}
		}
	}

	.base {
		padding: 0 0 10px 0;

		.line {
			margin: 15px 0;
		}
	}

	.table_wrap {

		// height:500px;
		// .tabs{
		// 	margin: 15px 0;
		// 	border-bottom: 2px solid $base-color;
		// 	.tab{
		// 		padding:10px 15px;
		// 		cursor: pointer;	
		// 		&:hover{
		// 			color:$base-color;
		// 		}
		// 		&.active{
		// 			background: $base-color;
		// 			color:#fff;
		// 			border-top-left-radius: 4px;
		// 			border-top-right-radius: 4px;
		// 		}
		// 	}
		// }
		.btns {
			margin-bottom: 15px;

			.btn {
				margin-left: 15px;
			}
		}

		.table_top {
			margin-bottom: 15px;

			.btn {
				width: 100px;
				margin-left: 15px;
			}
		}

		.tips {
			margin-bottom: 20px;

			view {
				margin-right: 15px;
				font-size: 14px;

				text {
					color: #0092ff;
				}
			}

			.tip {
				color: #f00;
			}
		}
	}
</style>